@import url(https://fonts.googleapis.com/css?family=Rubik:300,300i,400,400i,500,500i,700,700i,900&display=swap);
:root {
  --border-radius: 4px;
  --space: 13px; }

.App {
  --color-soft: #eee;
  --bg-color: #e4f3ff;
  --text-color: #323232;
  --text-color-soft: #ccc;
  --text-color-mid: rgb(148, 148, 148);
  --border-color-soft: #eee;
  --bg-color-card: #fff;
  --box-shadow: 0px 2px 4px 2px #94949478;
  --bg-color-input: #fff;
  --bg-color-weather-details: #06bcfb;
  --bg-image-weather-details: linear-gradient(0deg, #06bcfb 0%, #4885eebd 74%);
  --text-color-weather-details: #fff;
  --bg-color-day-weather-details: lightblue;
  --bg-color-night-weather-details: rgb(33, 76, 109);
  --bg-color-header: #06bcfb;
  --bg-image-header: linear-gradient(0deg, #06bcfb 0%, #4f81d8bd 74%); }

.App[data-theme="dark"] {
  --color-soft: rgb(68, 68, 68);
  --bg-color: #6d6d6d;
  --text-color: #fff;
  --text-color-soft: #ccc;
  --text-color-mid: rgb(148, 148, 148);
  --box-shadow: 0px 2px 4px 2px #29292978;
  --bg-color-card: #333333;
  --bg-color-weather-details: #0091aa;
  --bg-image-weather-details: linear-gradient(0deg, rgb(0, 50, 89) ; 0%, #4885eebd 74%);
  --bg-color-day-weather-details: #6aa1b3;
  --bg-color-night-weather-details: rgb(0, 50, 89);
  --bg-color-header: #004d66;
  --bg-image-header: linear-gradient(0deg, #005d7c 0%, #2567d8bd 74%);
  --bg-color-input: #333333; }

* {
  -webkit-transition: background-color 0.7s;
  transition: background-color 0.7s;
  -webkit-transition-delay: 0;
          transition-delay: 0; }

* {
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: "Rubik", sans-serif; }

html, body {
  width: 100%;
  overflow-x: hidden; }

button {
  cursor: pointer;
  background-color: unset;
  padding: unset; }

input {
  background-color: var(--bg-color-input); }

button,
input {
  outline: none;
  border: none;
  font-size: 15px;
  color: unset; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

a {
  color: inherit;
  text-decoration: none;
  /* no underline */ }

.container {
  max-width: 1100px;
  margin: 0 auto; }

.content {
  padding: 15px; }

.flex {
  display: flex; }

.column {
  flex-direction: column; }

.floating-card {
  background-color: var(--bg-color-card);
  border-radius: 2px;
  height: 200px; }

.responsive-grid {
  display: grid;
  grid-column-gap: 13px;
  grid-column-gap: var(--space);
  grid-row-gap: 13px;
  row-gap: 13px;
  grid-row-gap: var(--space);
  row-gap: var(--space);
  margin: 13px 0;
  margin: var(--space) 0;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr; }
  @media (max-width: 1000px) {
    .responsive-grid {
      grid-template-columns: 1fr 1fr 1fr 1fr; } }
  @media (max-width: 670px) {
    .responsive-grid {
      grid-template-columns: 1fr 1fr; } }

.App {
  min-height: 100vh;
  background-color: var(--bg-color);
  color: var(--text-color); }

.app-header-cmp {
  color: #fff;
  padding: 0 10px;
  width: 105%;
  height: 100px;
  left: -3%;
  position: relative;
  background-color: var(--bg-color-header);
  background-image: var(--bg-image-header);
  border-bottom-left-radius: 50% 40%;
  border-bottom-right-radius: 50% 40%; }
  .app-header-cmp .container {
    padding: 15px 20px; }
  .app-header-cmp .widget-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 6px;
    font-size: 16px; }
    @media (max-width: 670px) {
      .app-header-cmp .widget-container {
        font-size: 14px; } }
  .app-header-cmp .main {
    display: flex;
    justify-content: space-between;
    align-items: center; }

.nav-bar-cmp a {
  margin-left: 11px;
  font-size: 19px;
  padding-bottom: 3px; }
  .nav-bar-cmp a.active {
    border-bottom: 2px solid; }

.toggle-unit-cmp > * {
  opacity: 0.6; }

.toggle-unit-cmp button {
  background-color: unset;
  color: #fff;
  padding: 0;
  font-size: unset; }
  .toggle-unit-cmp button.active {
    opacity: 1; }


.home-page .location-search-cmp {
  margin-top: 20px;
  margin-bottom: 30px; }

.location-search-cmp {
  position: relative; }
  .location-search-cmp input {
    font-size: 17px;
    width: 100%;
    padding: 15px;
    border-radius: 9px; }
  .location-search-cmp .suggests-list-cmp {
    position: absolute;
    width: 100%;
    top: 110%; }

.suggests-list-cmp {
  z-index: 1;
  background-color: var(--bg-color-card);
  box-shadow: var(--box-shadow);
  max-height: 300px;
  overflow-y: scroll;
  border-bottom-left-radius: 9px;
  border-bottom-right-radius: 9px; }
  .suggests-list-cmp .suggest-preview {
    cursor: pointer;
    border-bottom: 1px solid var(--border-color-soft);
    font-size: 15px;
    padding: 1rem;
    -webkit-transition: background-color 0s;
    transition: background-color 0s; }
    .suggests-list-cmp .suggest-preview:hover {
      background-color: var(--color-soft); }
    .suggests-list-cmp .suggest-preview .country {
      color: var(--text-color-mid);
      font-weight: 300;
      margin-left: 3px;
      font-size: 14px; }

.location-details-cmp .top {
  display: flex;
  justify-content: space-between;
  background-color: var(--bg-color-weather-details);
  background-image: var(--bg-image-weather-details);
  color: var(--text-color-weather-details);
  margin-bottom: var(--space);
  height: 220px; }
  .location-details-cmp .top > * {
    width: 33%; }
  .location-details-cmp .top .name-temperature-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
    .location-details-cmp .top .name-temperature-container .temperature {
      padding-left: 5px; }
  .location-details-cmp .top .weather-icon-text {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 28px; }
    .location-details-cmp .top .weather-icon-text img {
      margin-bottom: 10px;
      width: 70px;
      height: 70px; }
  .location-details-cmp .top .toggle-heart-cmp {
    text-align: right; }

.location-details-cmp .time-wind-container {
  display: grid;
  grid-column-gap: var(--space);
  -webkit-column-gap: var(--space);
     -moz-column-gap: var(--space);
          column-gap: var(--space);
  grid-row-gap: var(--space);
  row-gap: var(--space);
  grid-template-columns: 3fr 2fr; }
  @media (max-width: 670px) {
    .location-details-cmp .time-wind-container {
      grid-template-columns: 1fr; } }

.location-details-cmp .location-time-card {
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: var(--bg-color-day-weather-details); }
  .location-details-cmp .location-time-card.night {
    background-color: var(--bg-color-night-weather-details); }
  .location-details-cmp .location-time-card h3 {
    font-weight: 300; }

@-webkit-keyframes flutter {
  0% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); }
  50% {
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9); }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); } }

@keyframes flutter {
  0% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); }
  50% {
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9); }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); } }

.location-details-cmp .wind {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }
  .location-details-cmp .wind img {
    -webkit-animation: flutter 2.5s infinite;
            animation: flutter 2.5s infinite;
    position: absolute;
    opacity: 0.8;
    height: 75%;
    left: 3%;
    bottom: 9%;
    -webkit-transform-origin: left;
            transform-origin: left; }


.day-forecast-cmp {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center; }
  .day-forecast-cmp .day {
    border-bottom: 1px solid #ccc; }
  .day-forecast-cmp .icon-temperature-container {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .day-forecast-cmp .icon-temperature-container img {
      width: 33px;
      margin-bottom: 15px; }

.toggle-heart-cmp {
  font-size: 24px;
  color: #ccc; }
  .toggle-heart-cmp i {
    cursor: pointer; }
  .toggle-heart-cmp.active {
    color: tomato; }
  .toggle-heart-cmp:hover {
    color: rgba(255, 99, 71, 0.678); }




.location-preview-cmp {
  cursor: pointer;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center; }
  .location-preview-cmp:hover {
    background-color: #fff; }
  .location-preview-cmp .icon-temperature-container {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .location-preview-cmp .icon-temperature-container img {
      width: 33px;
      margin-bottom: 15px; }

